import * as React from 'react';

import {
    Card, CardImage, CardHeader,
    CardTitle, CardSubtitle,
    CardBody, CardActions
} from '@progress/kendo-react-layout';

export const Monitors = (props: any) => {
    const text = (
      <React.Fragment>
        <IpsMonitors />
        <VaMonitors />
      </React.Fragment>
    );

    return (
      <div>
        {props.children ? props.children : text}
      </div>
    );
}

